import projects from '../apis/projects'
import history from '../history';

export const fetchProjects = () => {
    return async (dispatch) => {
        const response = await projects.get('/projects');

        dispatch({ type: 'FETCH_PROJECTS', payload: response.data });
    }
}

export const createProject = (formValues) => {
    return async (dispatch) => {
        let counter = await (await projects.get('/projectCount'));
        counter = parseInt(counter.data.counter);
        counter++;
        counter = counter.toString();
        const id = await projects.post('/projectCount', { counter });
        const response = await projects.post('/projects', {id: id.data.counter, ...formValues})
    
        dispatch({ type: 'CREATE_PROJECT', payload: response.data });
        //Programmatic Navigation
        history.push('/admin');
    }
}

export const editProject = (formValues, id) => {
    return async (dispatch) => {
        const  response = await projects.patch(`/projects/${id}`, formValues);
        dispatch({ type: 'EDIT_PROJECT', payload: response.data });
        //Programmatic Navigation
        history.push('/admin');
    }
}

export const deleteProject = (id_) => {
    return async (dispatch) => {
        const response = await projects.delete(`projects/${id_}`);
        let counter = await (await projects.get('/projectCount'));
        counter = parseInt(counter.data.counter);
        counter--;
        counter = counter.toString();
        const id = await projects.post('/projectCount', { counter });

        dispatch({ type: 'DELETE_PROJECT', payload: response.data });
        //Programmatic Navigation
        history.go('/admin');
    }
}