import './ImageGrid.css';
import React from 'react';

class ImageGrid extends React.Component {
    
    renderProjects = () => {
        return this.props.projects.map((project) => {
            return(
                <div className="column column-item">
                    <div className="content-container">
                        <h3>
                            {project.projectName}
                        </h3>
                    </div>
                    <div className="image-container">
                        <div>
                            <img id={`project-logo${project.id}`} key={project.id} src={project.projectLogo} />
                        </div>
                    </div>
                    <div className="button-container">
                        <div 
                            style={{margin: '0px'}} 
                            className="ui button" 
                            onClick={() => {
                                        (this.props.formProps.gridCardButton === 'Edit') 
                                        ? this.props.onEditClick(project) 
                                        : this.props.onDeleteClick(project) 
                                            }
                                    }
                        >{this.props.formProps.gridCardButton}</div>
                    </div>
                </div>
            );
        });
    }

    render() {
        return(
            <div className="grid-container">
                <div style={{paddingBottom: '50px' , textAlign: 'center'}}><h2>{this.props.formProps.gridTitle}</h2></div>
                <div style={{display: 'flex', justifyContent: 'center'}} className="ui stackable six column grid">
                    {this.renderProjects()}
                </div>
            </div>
        );
    }
}

export default ImageGrid;